body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  height: 100%;
  width: 100%;
  display: block;
}
  .container form {
    margin: 0 auto;
    max-width: 355px;
    text-align: left;
    margin-bottom: 50px;

  }

  #firstName {
    margin-right: 3px;
  }

  #lastName {
    margin-left: 3px;
  }

  #firstAndLastName {
    display: flex;
    flex-direction: row;

  }

  .container {
    text-align: center;
    background-color: inherit;
  }

  .content {
  text-align: center;
  }

  .img-responsive {
    margin: 10px;
    width: 100%;
    max-width: 175px;
  }

  .h3 {
    margin-bottom: 10px;
  }

  #unsubscribeBtn {
    max-width: 50%;
    margin: 0 auto;
    margin-top: 30px;
  }


  p {
    margin: 0 auto;
    max-width: 600px;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }

  body {
    background-color: #f8f9fa;
    height: 100%;
  }

  .users {
    display: inline-block;
    border: solid 1px #007bff;
    border-radius: 10px;
    width: 15%;
    margin: 10px 10px;
  }

  ul {
    list-style: none;
  }

  li {
    float: left
  }

